<template>
  <div class="chart-wrapper" ref="liquidChart"></div>
</template>

<script>
import "echarts-liquidfill";

export default {
  name: "liquidChart",
  mounted() {
    //渲染echarts图
    const myChart = this.$echarts.init(this.$refs.liquidChart);
    myChart.setOption(
      {
        series: [
          {
            type: "liquidFill",
            radius: "85%",
            center: ["50%", "45%"],
            data: [0.48, 0.48, 0.48, 0.48],
            backgroundStyle: {
              color: {
                type: "linear",
                x: 1,
                y: 0,
                x2: 0.5,
                y2: 1,
                colorStops: [
                  {
                    offset: 1,
                    color: "rgba(68, 145, 253, 0.55)",
                  },
                  {
                    offset: 0.5,
                    color: "rgba(68, 145, 253, 0.65)",
                  },
                  {
                    offset: 0,
                    color: "rgba(68, 145, 253, 1)",
                  },
                ],
                globalCoord: false,
              },
            },
            outline: {
              borderDistance: 0,
              itemStyle: {
                borderWidth: 5,
                borderColor: {
                  type: "linear",
                  x: 0,
                  y: 0,
                  x2: 0,
                  y2: 1,
                  colorStops: [
                    {
                      offset: 0,
                      color: "rgba(53,142,215, 0)",
                    },
                    {
                      offset: 0.5,
                      color: "rgba(53,142,215, 0.45)",
                    },
                    {
                      offset: 1,
                      color: "rgba(53,142,215, 0.9)",
                    },
                  ],
                  globalCoord: false,
                },
                shadowBlur: 10,
                shadowColor: "rgba(53,142,215, 0.9)",
              },
            },
            label: {
              normal: {
                formatter: "节点在线：" + 1,
                textStyle: {
                  fontSize: 18,
                },
              },
            },
          },
        ],
      },
      true
    );
  },
};
</script>

<style scoped>
.chart-wrapper {
  width: 100%;
  height: 300px;
}
</style>
