<template>
  <div class="line-race" ref="line"></div>
</template>

<script>
export default {
  name: "LineRace",
  props: {
    xData: {
      type: Array,
      default: ["", "", "", "", "", "", "", "", ""],
    },
    seriesData: {
      type: Array,
      default: [],
    },
  },
  data() {
    return {
      chart: null,
      option: null,
    };
  },
  mounted() {
    this.chart = this.$echarts.init(this.$refs.line);
    this.option = {
      xAxis: {
        type: "category",
        data: this.xData,
      },
      yAxis: {
        type: "value",
      },
      tooltip: {
        trigger: "axis",
        formatter: function (params) {
          console.log(params);
          params = params[0];
          return params;
        },
        axisPointer: {
          animation: false,
        },
      },
      series: [
        {
          data: this.seriesData,
          type: "line",
          left: 0,
          top: 0,
          width: "100%",
          height: 500,
          smooth: true,
          itemStyle: { normal: { label: { show: true } } },
        },
      ],
    };
  },
};
</script>

<style scoped>
.line-race {
  width: 100%;
  height: 500px;
}
</style>
