import axios from 'axios'

export function request(config) {
  const instance = axios.create({
    baseURL: '//47.106.221.121:8080',
    timeout: 5000
  })

  instance.interceptors.request.use(
    config => {
      return config
    }, err => {
      console.log(err)
    }
  )

  instance.interceptors.response.use(
    res => {
      return res.data
    }, err => {
      console.log(err)
    }
  )

  return instance(config)
}