<template>
  <div class="home">
    <header><span>物联网节点数据展示平台</span></header>
    <four-angel>
      <liquid-chart />
    </four-angel>
    <four-angel class="line1">
      <line-race :seriesData="seriesData1" ref="line1"></line-race>
    </four-angel>
    <four-angel class="line2">
      <line-race :seriesData="seriesData2" ref="line2"></line-race>
    </four-angel>
    <four-angel class="line3">
      <word-cloud />
    </four-angel>
  </div>
</template>

<script>
import WordCloud from "components/WordCloud";
import LineRace from "components/LineRace";
import FourAngel from "components/fourAngel/index";
import LiquidChart from "components/liquidChart";

import { getInit, getData } from "network/home";

export default {
  name: "Home",
  components: {
    WordCloud,
    LineRace,
    FourAngel,
    LiquidChart,
  },
  data() {
    return {
      tem: "",
      seriesData1: [],
      seriesData2: [],
    };
  },
  methods: {
    getInfinite() {
      getData().then((res) => {
        this.tem = res;
        if (this.seriesData1.length > 8) this.seriesData1.shift();
        this.seriesData1.push(res.split(",")[1]);
        this.$refs.line1.chart.setOption(this.$refs.line1.option);
        if (this.seriesData2.length > 8) this.seriesData2.shift();
        this.seriesData2.push(res.split(",")[0]);
        this.$refs.line2.chart.setOption(this.$refs.line2.option);
        this.getInfinite();
      });
    },
    getSeriesData() {
      return 28 + Math.random() * 3;
    },
  },
  mounted() {
    getInit().then((res) => {
      console.log(res);
      getData().then((res) => {
        this.tem = res;
      });
    });
    this.getInfinite();

    // setInterval(() => {

    // }, 1000);
  },
};
</script>

