<template>
  <div id="word-cloud"></div>
</template>

<script>
import "echarts-wordcloud";
export default {
  name: "WordCloud",
  methods: {
    eConsole(param) {
      if (typeof param.seriesIndex != "undefined") {
        switch (param.name) {
          case "郭梓杰":
            this.$router.push("/profile");
            break;
          default:
            break;
        }
      }
    },
  },
  mounted() {
    const data = [
      {
        name: "郭梓杰",
        value: 25,
      },
      {
        name: "陈梓浩",
        value: 20,
      },
      {
        name: "高沧龙",
        value: 20,
      },
      {
        name: "唐国强",
        value: 20,
      },
      {
        name: "肖子雄",
        value: 20,
      },
      {
        name: "苏伟鹏",
        value: 20,
      },
      {
        name: "陈吉虹",
        value: 20,
      },
      {
        name: "郑建宇",
        value: 20,
      },
    ];
    const chart = this.$echarts.init(document.getElementById("word-cloud"));
    chart.setOption({
      series: [
        {
          type: "wordCloud",
          gridSize: 1,
          sizeRange: [20, 25],
          rotationRange: [-45, 0, 45, 90],
          textStyle: {
            normal: {
              color: function () {
                return (
                  "rgb(" +
                  Math.round(Math.random() * 255) +
                  ", " +
                  Math.round(Math.random() * 255) +
                  ", " +
                  Math.round(Math.random() * 255) +
                  ")"
                );
              },
            },
          },
          left: "center",
          top: "center",
          width: "100%",
          height: "100%",
          data,
        },
      ],
    });
    chart.on("click", this.eConsole);
  },
};
</script>

<style scoped>
#word-cloud {
  width: 100%;
  height: 150px;
}
</style>